.closed .switcher.down { transform: rotate(-90deg) scaleX(-1); }

.switcher.left { transform: scale(-1, 1); }

.switcher.switcher_slider { --w:calc(38px / 2); --h:calc(70px / 2); width: var(--w); height: var(--h); background: url(/images/map/btns/arrow.png) no-repeat 0 0; background-size: cover; z-index: 5; pointer-events: auto; }

.slide_at_left .switcher.switcher_slider { transform: translateX(-50%) scale(-1, 1); }

.slide_at_left.closed .switcher.switcher_slider { transform: translateX(-50%) scale(1, 1); }

.slide_at_right .switcher.switcher_slider { transform: translateX(50%) scale(1, 1); }

.slide_at_right.closed .switcher.switcher_slider { transform: translateX(50%) scale(-1, 1); }

.dropdown_item .switcher.switcher_slider.down { transform: rotate(-90deg) scaleX(-1); }

.dropdown_item:hover .switcher.switcher_slider.down { transform: rotate(-90deg); transition: var(--animation_hovered); filter: saturate(2) brightness(1.5); }

.panel_army.slide_at_right .switcher_slider { transform: translate(-50%, -50%) scale(1, 1); }

.panel_army.slide_at_right.closed .switcher_slider { transform: translate(-50%, -50%) scale(-1, 1); }

.close_btn { border: 0; padding: 0; border-radius: 6px; --s:calc(56px / 2); width: var(--s); height: var(--s); background: url(/images/map/btns/close.png) no-repeat 0 0; background-size: cover; display: block; top: 3px; right: 3px; z-index: 2; }

.mob_btn .panel_holder { width: 65px; height: 65px; align-items: center; justify-content: center; }

.mob_btn .decor:after, .mob_btn .decor:before { content: none; }

.mob_btn .corner { background: var(--decorate_img) 0 0 no-repeat; background-size: cover; --width:calc(var(--size_w) / 2); --height:calc(var(--size_h) / 2); }

